    <Header />
    <router-view />
  </div>
</template>

<script>
import '@/assets/style/normalize.css'
import Header from '@/components/Header'
export default {
  name: 'App',
  components:{
    Header
  }
}
</script>

<style>
body{
  background: url(./assets/img/index-bg.jpg) no-repeat;
  background-color: rgb(34, 34, 34);
  background-position:60% 20%;
}
#app{
  background-position:60% 50%;
  color: #fff;
  font-size: 12px;
}
.container{
  width:7.5805rem;
  margin: 0 auto;
}
.clearfix:after{
    content:"";
    display:block;
    height:0;
    visibility:hidden;
    clear:both;
}
@keyframes moveMedia{
  from{
    margin-top:200px;
  }
  to{
    margin-top:10px;
  }
}
</style>



